import React, { useState } from "react";
import styled from "styled-components";
import Modal from "react-modal";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper/modules";

const images = [
  "/gallery/image01.jpeg",
  "/gallery/image02.jpeg",
  "/gallery/image03.jpeg",
  "/gallery/image04.jpeg",
  "/gallery/image05.jpeg",
  "/gallery/image06.jpeg",
  "/gallery/image07.jpeg",
  "/gallery/image08.jpeg",
  "/gallery/image09.jpeg",
];

const GalleryPage = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0);

  const openModal = (index) => {
    setSelectedIndex(index);
    setIsOpen(true);
  };

  const closeModal = () => {
    setIsOpen(false);
  };

  return (
    <Section id="section2">
      <TitleMsg>사진첩</TitleMsg>
      <GalleryContainer>
        {images.map((img, idx) => (
          <ImageItem key={idx} onClick={() => openModal(idx)}>
            <img src={img} alt={`Gallery ${idx}`} />
          </ImageItem>
        ))}
      </GalleryContainer>

      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={{
          content: {
            position: "fixed",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            backgroundColor: "transparent",
            border: "none",
            padding: 0,
            left: 0,
            right: 0,
          },
          overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            zIndex: 999,
          },
        }}
      >
        <ModalContent>
          <Swiper
            initialSlide={selectedIndex}
            spaceBetween={30}
            slidesPerView={1}
            centeredSlides={true}
            onSlideChange={(swiper) => setSelectedIndex(swiper.activeIndex)}
            style={{ width: "100vw", height: "90vh" }}
            modules={[Navigation]}
            navigation={{
              prevEl: ".custom-prev",
              nextEl: ".custom-next",
            }}
          >
            {images.map((img, idx) => (
              <SwiperSlide key={idx}>
                <ImageWrapper>
                  <img src={img} alt={`Slide ${idx}`} onClick={closeModal} />
                </ImageWrapper>
              </SwiperSlide>
            ))}
          </Swiper>

          <ArrowLeft visible={selectedIndex !== 0} className="custom-prev">
            <img src="/images/left_arrow.png" alt="Left Arrow" />
          </ArrowLeft>

          <ArrowRight
            visible={selectedIndex !== images.length - 1}
            className="custom-next"
          >
            <img src="/images/right_arrow.png" alt="Right Arrow" />
          </ArrowRight>

          {/* {selectedIndex !== 0 && (
            <>
              <ArrowLeft className="custom-prev">
                <img src="/images/left_arrow.png" alt="Left Arrow" />
              </ArrowLeft>
            </>
          )}
          {selectedIndex !== images.length - 1 && (
            <>
              <ArrowRight className="custom-next">
                <img src="/images/right_arrow.png" alt="Right Arrow" />
              </ArrowRight>
            </>
          )} */}
        </ModalContent>
      </Modal>
    </Section>
  );
};

export default GalleryPage;

const Section = styled.div`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 10px;
`;

const TitleMsg = styled.div`
  font-family: "Nanum Myeongjo", serif;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.16em;
  line-height: 1.4em;
  margin-top: 60px;
  margin-bottom: 30px;
`;

const GalleryContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  max-width: 800px;
  width: 100%;
  height: auto;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
`;

const ImageItem = styled.div`
  position: relative;
  width: 100%;
  padding-top: 100%;
  overflow: hidden;
  cursor: pointer;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transform: translate(-50%, -50%);
  }
`;

const ModalContent = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: relative;
`;

const ImageWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - 80px);
  height: 100%;
  margin: 0 auto;

  img {
    max-width: 90%;
    max-height: 90%;
    object-fit: contain;
    cursor: pointer;
  }
`;

const ArrowLeft = styled.div`
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  z-index: 1000;
  cursor: pointer;
  opacity: ${(props) => (props.visible ? 1 : 0)};

  img {
    width: 30px;
    height: auto;
  }
`;

const ArrowRight = styled.div`
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  z-index: 1000;
  cursor: pointer;
  opacity: ${(props) => (props.visible ? 1 : 0)};

  img {
    width: 30px;
    height: auto;
  }
`;
