import React, { useState, useEffect } from "react";
import { Link, scroller } from "react-scroll";
import styled from "styled-components";
import MainPage from "./pages/MainPage";
import GreetingsPage from "./pages/GreetingsPage";
import GalleryPage from "./pages/GalleryPage";

const App = () => {
  const [showNav, setShowNav] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY >= window.innerHeight*0.99) {
        setShowNav(true);
      } else {
        setShowNav(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const handleSetActive = (section) => {
    if (section === "section1" || section === "section2") {
      setShowNav(true); // 네비게이션 바를 표시
    }
  };

  return (
    <div>
      <Nav showNav={showNav}>
        <NavLink
          to="section1"
          spy={true}
          smooth={true}
          duration={500}
          onSetActive={() => handleSetActive("section1")}
        >
          인사말
        </NavLink>
        <NavLink
          to="section2"
          spy={true}
          smooth={true}
          duration={500}
          onSetActive={() => handleSetActive("section2")}
        >
          사진첩
        </NavLink>
      </Nav>

      <MainPage />
      <GreetingsPage />
      <GalleryPage />
    </div>
  );
};

export default App;

// Styled Components for App.js
const Nav = styled.nav`
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #ffffe6;
  z-index: 10;
  display: flex;
  justify-content: space-around;
  padding: 0.6rem 0;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.5s ease, opacity 0.5s ease;
  opacity: ${(props) => (props.showNav ? 1 : 0)};
  transform: translateY(${(props) => (props.showNav ? "0" : "-100%")});
`;

const NavLink = styled(Link)`
  color: #333;
  text-decoration: none;
  cursor: pointer;
`;
